	module.exports = {"data":{"markdownRemark":{"html":"<p>나는 이탈리아 레스토랑에서 일본 국제 포럼에서 일본 학자들과 아침 식사를했다. 이 기간 동안 다음과 같은 몇 가지 쟁점이 논의되고 요약되었습니다.</p>\n<p>최근 나가사키 대학살에 대한 태도에 관한 일본 법무부 장관의 이러한 태도는 본질적으로 일부 일본 정치인들의 견해를 반영한 ​​것이지만, 국제 사회의 압박감에 따라 여당이 양 측을 양보하고 영계가 사퇴하게 만들 수는 없었다. . 나가노 현 총재는 이날 야누코비치 총재와 가진 인터뷰에서 나가노 현 총리와 회담 한 뒤 기자 회견을 갖고 이같이 밝혔다. 그러나, 그것은 \"난징 대학살\"을 인정했다. 신문은 영계가 \"남경 대학살\"자체를 부인했다고 보도했다. 이것은 설명이 될 수도 있습니다. 어떤 경우이든,이 사건은 많은 고위 일본 관료들이이 문제에 대해 깊은 이해를하지 못했다는 사실을 반영한다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0090.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0090.md","frontmatter":{"title":"5월 15일 일요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0090.md absPath of file >>> MarkdownRemark"}}

